<template>
  <div>
    <van-swipe :autoplay="3000" @change="onChange" :height="232">
      <van-swipe-item v-for="(image, index) in imgarr" :key="index">
        <router-link :to="{name:'slidedetail',params:{id:index}}"><img v-lazy="image"></router-link>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  name: "slidecard",
  data() {
    return {
      imgarr: [
        require("../assets/card1.jpg"),
        require("../assets/card2.jpg"),
        require("../assets/card3.jpg"),
        require("../assets/card4.jpg"),
        require("../assets/card5.jpg")
      ]
    };
  },
  methods: {
    onChange(index) {
      // console.log("当前 Swipe 索引：" + index);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* slider */
.van-swipe-item img {
  width: 100%;
  opacity: 0.75;
  margin: 0;
}
</style>



