<template>
  <div>
    <van-nav-bar title="Welcome to 粽*47自闭网！"/>
    <slidecard id="slidecard"/>
    <h3>正在热映</h3>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div v-for="(e,i) in list" :key="i" class="filmli">
        <img :src="e.images.small">
        <p>{{e.title}}</p>
      </div>
    </van-list>
  </div>
</template>

<script>
import slidecard from "../components/slidecard";

export default {
  name: "hellozsq",
  components: {
    slidecard
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      pageindex: 0,
      total: Number
    };
  },
  created() {
    this.getflimlist();
  },
  methods: {
    getflimlist() {
      this.$axios
        .get("/api/movie/in_theaters", {
          params: {
            start: this.pageindex,
            count: 22,
            city: "南京"
          }
        })
        //成功返回
        .then(({ data }) => {
          this.list = this.list.concat(data.subjects);
          this.total = data.total;
        })
        //失败返回
        .catch(error => {
          console.log(error);
        });
    },
    onLoad() {
      // this.pageindex = this.pageindex + this.list.length;
      // alert(this.pageindex);
      // console.log()
      // this.getflimlist();
      // 异步更新数据

      this.loading = false;
      // 数据全部加载完成
      if (this.list.length >= this.total) {
        this.finished = true;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.van-nav-bar {
  background: #4682b4;
  color: #f0f8ff;
}
.van-list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.filmli {
  width: 30%;
}
.filmli img {
  width: 100%;
  height: 180px;
  padding: 5px;
  box-sizing: border-box;
}
.filmli p {
  margin: 5px;
  font-size: 14px;
  color: #666;
}
h3 {
  padding-left: 10px;
  text-align: left;
  border-left: 5px solid #4682b4;
  margin: 10px 0;
}
</style>
<style>
.van-list__loading {
  width: 100%;
}
.van-nav-bar__title {
  font-size: 20px;
}
.van-nav-bar {
  padding: 5px;
}
</style>



