import Vue from 'vue'
import Router from 'vue-router'
import hellozsq from '@/components/hellozsq'
import slidedetail from '@/components/slidedetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/hellozsq'
    },
    {
      path: '/hellozsq',
      name: 'hellozsq',
      component: hellozsq,
    },
    {
      path: '/slidedetail',
      name: 'slidedetail',
      component: slidedetail
    }
  ]
})



